.home {
    height:50vh;
    margin-left: auto;
    margin-right: auto;
    margin-top:10%;
    width:25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.form {
    position: relative;
    height: 40px;
    width: 100%;
    overflow:hidden;
    background-color:rgba(255,255,255,0.1);
    border-radius: 10px;
}
.form input {
    width: 100%;
    height: 100%;
    border: none;
    outline:none;
    font-size: 22px;
    text-align: center;
    background:rgba(255, 255, 255, 0.8);
    font-family:"Segoe print", Arial, Helvetica, sans-serif;
    color:rgb(247, 95, 7);
    padding:24px 0px 32px 0px;
    font-weight:lighter;
    text-align:center;
    display:inline;
    
}
.form label {
    position:absolute;
    padding-top: 20px;
    bottom:0px;
    left:0px;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1.5px solid rgb(255, 255, 255);
    border-left: 1.5px solid rgb(255, 255, 255);
    border-right: 1.5px solid rgb(255, 255, 255);
    transform:translateY(29%);
}
.form label::after {
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 2px solid #ffffff;
    transform:translateX(-100%);
}
.form input:focus + .label-name::after,
.form input:valid + .label-name::after {
    transform: translateX(0%);
}
.form-button {
    position: relative;
    height: 40px;
    width: 70%;
    overflow:hidden;
}

.button {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    background:crimson;
    color:#fff;
    font-family:"Segoe print", Arial, Helvetica, sans-serif;
    border:1pxsolid#eee;
    border-radius:20px;
    box-shadow:5px5px5px#eee;
    text-shadow:none;
}



