.form {
    position: relative;
    height: 40px;
    width: 100%;
    overflow:hidden;
    background-color:rgba(255,255,255,0.1);
    border-radius: 10px;
}
.form input {
    width: 100%;
    height: 100%;
    border: none;
    outline:none;
    font-size: 22px;
    text-align: center;
    background:rgba(255, 255, 255, 0.8);
    font-family:"Segoe print", Arial, Helvetica, sans-serif;
    color:rgb(247, 95, 7);
    padding:24px 0px 32px 0px;
    font-weight:lighter;
    text-align:center;
    display:inline;
    
}
.form label {
    position:absolute;
    padding-top: 20px;
    bottom:0px;
    left:0px;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1.5px solid rgb(255, 255, 255);
    border-left: 1.5px solid rgb(255, 255, 255);
    border-right: 1.5px solid rgb(255, 255, 255);
    transform:translateY(29%);
}
.form label::after {
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 2px solid #ffffff;
    transform:translateX(-100%);
}
.form input:focus + .label-name::after,
.form input:valid + .label-name::after {
    transform: translateX(0%);
}
.form-button {
    position: relative;
    height: 40px;
    width: 70%;
    overflow:hidden;
}

.button {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    background:crimson;
    color:#fff;
    font-family:"Segoe print", Arial, Helvetica, sans-serif;
    border:1pxsolid#eee;
    border-radius:20px;
    box-shadow:5px5px5px#eee;
    text-shadow:none;
}




i {
  margin-right: 10px;
}
.navbar-logo {
  padding: 15px;
  color: #fff;
  pointer-events: none;
}

.navbar-mainbg {
  background-color:rgb(0,123,255);
  padding: 0px;
}
#navbarSupportedContent {
  overflow: hidden;
  position: relative;
}
#navbarSupportedContent ul {
  display: flex;
  align-items: center;
  justify-content:space-evenly;
  padding: 0px;
  margin: 0px;
}
#navbarSupportedContent ul li a i {
  margin-right: 10px;
}
#navbarSupportedContent li {
  list-style-type: none;
  float: left;
}
#navbarSupportedContent ul li a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 17.5px;
  display: block;
  padding: 20px 20px;
  transition-duration: 0.6s;
  position: relative;
}

#navbarSupportedContent a:not(:only-child):after {
  content: '\f105';
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 14px;
  font-family: 'Font Awesome 5 Free';
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  transition: 0.5s;
}
#navbarSupportedContent .active > a:not(:only-child):after {
  transform: rotate(90deg);
}

.hori-selector {
  transition-duration: 0.6s;
  transition-timing-function: cubic-bezier(0, 0, 1, 1);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.hori-selector .right,
.hori-selector .left {
  position: absolute;
  width: 25px;
  height: 25px;
  bottom: 10px;
}
.hori-selector .right {
  right: -25px;
}
.hori-selector .left {
  left: -25px;
}
.hori-selector .right:before,
.hori-selector .left:before {
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #000000;
}
.hori-selector .right:before {
  bottom: 0;
  right: -25px;
}
.hori-selector .left:before {
  bottom: 0;
  left: -25px;
}
@media (max-width: 991px) {
  #navbarSupportedContent ul li a {
    padding: 12px 30px;
  }
  .hori-selector {
    margin-top: 0px;
    margin-left: 10px;
    border-radius: 0;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }
  .hori-selector .left,
  .hori-selector .right {
    right: 10px;
  }
  .hori-selector .left {
    top: -25px;
    left: auto;
  }
  .hori-selector .right {
    bottom: -25px;
  }
  .hori-selector .left:before {
    left: -25px;
    top: -25px;
  }
  .hori-selector .right:before {
    bottom: -25px;
    left: -25px;
  }
}
